import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { dev } from '../../config/dev';
import {DomSanitizer, SafeResourceUrl} from '@angular/platform-browser';




@Component({
  selector: 'app-pricingpage',
  templateUrl: 'pricingpage.component.html'
})
export class PricingpageComponent implements OnInit, OnDestroy {
  focus;
  isCollapsed = true;
  paid = false;

  url = dev.connect;
  templates: any;
  transactionId: any;
  userId: any;
  filename: any;

  iframe: any;
  urlSafe: SafeResourceUrl;
  private customize: any;
  constructor(private http: HttpClient,
              private router: Router,
              private activeroute: ActivatedRoute,
              public sanitizer: DomSanitizer
     ) {}

  ngOnInit() {
    let body = document.getElementsByTagName('body')[0];
    body.classList.add('pricing-page');
    let navbar = document.getElementById('navbar-main');
    navbar.classList.add('bg-white');
    navbar.classList.remove('navbar-dark');

    let logoImage: any = document.getElementById('brand-logo');
    logoImage.src = 'assets/img/brand/logo.png';

    //this.getValues();
    this.getQueryData();
    // this.encode();
   // this.getTemplatesPaidFor();
   //  this.urlSafe = this.sanitizer.bypassSecurityTrustResourceUrl(this.iframe);

  }
  ngOnDestroy() {
    let body = document.getElementsByTagName('body')[0];
    body.classList.remove('pricing-page');
    let navbar = document.getElementById('navbar-main');
    navbar.classList.remove('bg-white');
    navbar.classList.add('navbar-dark');

    let logoImage: any = document.getElementById('brand-logo');
    logoImage.src = 'assets/img/brand/logo.png';
  }



  encode(filename) {
     console.log(filename);
     const imgUrl = filename.split('8000')[1];
    //  this.iframe = ('https://www.photopea.com#' + encodeURI('{"files":["https://server.adiy.site/insta.jpg"],"environment": {}}')); 
     this.iframe  = ('https://www.photopea.com#' + encodeURI('{"files":["https://server.adiy.site') + encodeURI(imgUrl) + ('%22') + encodeURI('],"environment": {}}'));
     this.urlSafe = this.sanitizer.bypassSecurityTrustResourceUrl(this.iframe);
    // console.log(en);
  }
  getCartItems() {
    // const userID = localStorage.getItem('profile');
    // const userDet = {
    //   userid: userID,
    //   status: "Paid"
    // };
    // this.http.post(this.url + '/api/cart/paid', userDet).subscribe((cart) => {
    //   console.log(cart);
    //   this.templates = cart;
    // });
  }

  getTemplatesPaidFor() {

        if (this.userId != null) {
          const payload = {
            userId: this.userId
          };
          this.http.post(this.url + '/api/cart/paymentsbyuser', payload ).subscribe(cart => {
            console.log(cart);
            this.templates = cart;
            this.customize = this.templates[0].items.filename;
          });
        } else {
          console.log('No userid was passed, ... aborting');
        }

  }

  checkPaymentStatus(){

    const payload = {
      oid: this.transactionId,
      vid: 'genkey'
    }
    console.log(payload);
    this.http.post(this.url + '/api/cart/confirmPay', payload ).subscribe(data => {
        let paymentinfo: any; paymentinfo = data;
        if(paymentinfo.statusCode === 200){
          this.getTemplatesPaidFor();
          this.paid = true
        }else {
          this.getTemplatesPaidFor();
          this.paid = false;
          alert("It seems your payment has not been received yet.If you have paid, click the button to reload after a few seconds.")
        }
    });

  }
  getQueryData()
  {
    this.activeroute.queryParamMap.subscribe(params => {
      this.transactionId = params.get('tr');
      this.userId = params.get('id');
    })
  }


}
