import { Component, OnInit, OnDestroy } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { dev } from '../../config/dev';
import { FilesService } from '../../shared/files.service';

@Component({
  selector: 'app-ecommerce',
  templateUrl: 'ecommerce.component.html',
  styleUrls: ['./styles.css', './boardz.css', './boardz.min.css']
})
export class EcommerceComponent implements OnInit, OnDestroy {

  focus;
  focus1;
  focus2;
  focus3;

  products: any;
  url = dev.connect;
  profile: any;
  userDetails: any;
  cart: any = [];
  itemurl: any;
  name: any;
  color: any = 'white';
  colors: any = ['white', 'red', 'blue', 'green'];
  vertical: any;
  horizontal: any;
  Allfiles: any = [];

  constructor(private http: HttpClient , public FilesRestApi: FilesService) {
     this.getUser();
     this.loadFiles();

  }

  ngOnInit() {
    const body = document.getElementsByTagName('body')[0];
    body.classList.add('ecommerce-page');
    this.getUsersDesigns();

    //  const length = (this.products.length/3).toFixed(0);


  }
  ngOnDestroy() {
    const body = document.getElementsByTagName('body')[0];
    body.classList.remove('ecommerce-page');
  }

  loadmore() {
    (document.getElementById('loadmore').style.display = 'block');
    (document.getElementById('loadbtn').style.display = 'none');
  }
  less() {
    (document.getElementById('loadmore').style.display = 'none');
    (document.getElementById('loadbtn').style.display = 'block');
  }
  getUsersDesigns() {
    const payload = {
      userid: this.profile
    };
    return this.http.post(this.url + '/api/files/user', payload).subscribe((data) => {
      this.products = data;
    });
  }
  getUser() {
    this.profile = localStorage.getItem('profile');
   }

   addToCart(item) {
     // console.log(item);
     this.getUserDetails(item);
   }
  getUserDetails(item) {
    const amount = parseFloat(item.amount);
    const userID = localStorage.getItem('profile');
    const userDet = {
      userid: userID
    };
    this.http.post(this.url + '/api/cart/user', userDet).subscribe(cart => {
      this.cart = cart;
      if (this.cart.length > 0) {
        const items = this.cart[0].items;
        let amountc = this.cart[0].amount;
        amountc = amountc + amount;
        items.push(item);
        this.cart[0].items = items;
        this.cart[0].amount = amountc;
        this.http.post(this.url + '/api/cart/add', this.cart[0]).subscribe((response) => {
          console.log(response);
          alert('Added item to cart');
      });
     } else {
        // console.log("Doesnt Exist")
        const items = [];
        items.push(item);
        const payload = {
          user: userID,
          items,
          amount,
          status: 'Pending'
        };
        this.http.post(this.url + '/api/cart', payload).subscribe((response) => {
          alert('Added item to cart');
        });
      }

    }, error => {
       // console.log("Doesnt Exist")
       const items = [];
       items.push(item);
       const payload = {
         user: userID,
         items,
         amount,
         status: 'Pending'
       };
       this.http.post(this.url + '/api/cart', payload).subscribe((response) => {
         alert('Added item to cart');
       });
  });
  }
  editTemplate(product) {

  }
  myCanvas() {
    const c = document.getElementById('myCanvas') as HTMLCanvasElement;
    const ctx = c.getContext('2d');
    const img = document.getElementById('scream') as HTMLCanvasElement;
    ctx.font = '30px Roboto Condensed';
    ctx.fillStyle = this.color;
    ctx.drawImage(img, 0, 0);
    ctx.fillText(this.name, this.vertical, this.horizontal);
  }
  download() {

  }


  loadFiles() {
    return this.FilesRestApi.GetFiles().subscribe((data: {}) => {
      this.Allfiles = data;
    });
  }


  // Delete employee
  // tslint:disable-next-line:variable-name
  deleteFiles(_id)  {
    if (window.confirm('Are you sure, you want to delete?')) {
      this.FilesRestApi.DeleteFiles(_id).subscribe(data => {
        this.loadFiles();
      });
    }
  }
}
